.bolt-page > .bolt-tabbar {
  padding: 0 32px 0 24px; }
  @media screen and (max-width: 600px) {
    .bolt-page > .bolt-tabbar {
      padding-left: 12px;
      padding-right: 20px; } }

.bolt-page > .bolt-header {
  padding-top: 24px;
  padding-bottom: 0px;
  margin-left: 12px;
  margin-right: 12px;
  z-index: 3; }
  @media screen and (max-width: 600px) {
    .bolt-page > .bolt-header {
      padding-top: 16px;
      margin-left: 0;
      margin-right: 0; } }
  .bolt-page > .bolt-header .bolt-header-commandbar-no-right-padding {
    padding-right: 4px; }
    @media screen and (max-width: 600px) {
      .bolt-page > .bolt-header .bolt-header-commandbar-no-right-padding {
        padding-right: 0; } }

.bolt-page > .vss-FilterBar {
  margin-left: 32px;
  margin-right: 32px; }

.bolt-page .page-content {
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 16px; }
  @media screen and (max-width: 600px) {
    .bolt-page .page-content {
      padding-left: 0;
      padding-right: 0; } }

.bolt-page .page-content-top {
  padding-top: 16px; }

.bolt-page .page-content-bottom {
  padding-bottom: 16px; }

.bolt-page .page-content-left {
  padding-left: 32px; }
  @media screen and (max-width: 600px) {
    .bolt-page .page-content-left {
      padding-left: 0; } }

.bolt-page .page-content-right {
  padding-right: 32px; }
  @media screen and (max-width: 600px) {
    .bolt-page .page-content-right {
      padding-right: 0; } }

.bolt-page-grey {
  background-color: rgba( 248, 248, 248 ,  1 );
  background-color: rgba( var(--palette-neutral-2,248, 248, 248) ,  1 ); }

.bolt-page-white {
  background-color: rgba(255, 255, 255, 1);
  background-color: var(--background-color,rgba(255, 255, 255, 1)); }
