.bolt-header-no-spacing-defined {
  padding-left: 20px;
  padding-top: 16px;
  padding-right: 20px; }

.bolt-header-condensed {
  padding-top: 8px;
  padding-bottom: 8px; }

.bolt-header-default {
  padding-top: 16px;
  padding-bottom: 16px; }

.bolt-header-relaxed {
  padding-top: 24px;
  padding-bottom: 24px; }

.bolt-header-with-commandbar.bolt-header-condensed {
  padding-right: 12px; }

.bolt-header-with-commandbar.bolt-header-default {
  padding-right: 16px; }

.bolt-header-with-commandbar.bolt-header-relaxed {
  padding-right: 28px; }

.bolt-header-with-commandbar {
  padding-right: 16px; }
  @media screen and (max-width: 600px) {
    .bolt-header-with-commandbar {
      padding-right: 20px; } }

.bolt-header-with-back-button {
  padding-left: 16px; }
  @media screen and (max-width: 600px) {
    .bolt-header-with-back-button {
      flex-direction: column;
      padding-left: 20px; } }

.bolt-header-title-area {
  flex-shrink: 25; }

.bolt-header-content-area {
  min-width: 0px; }

.bolt-header-title {
  padding-top: 4px;
  padding-bottom: 4px;
  margin-right: 8px; }
  .bolt-header-title.l {
    padding-top: 0px;
    padding-bottom: 0px; }
  .bolt-header-title.s {
    padding-top: 6px;
    padding-bottom: 6px; }
  @media screen and (max-width: 600px) {
    .bolt-header-title {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis; } }

.bolt-header-icon {
  font-size: 1rem;
  margin-right: 8px;
  margin-top: 8px; }
  .bolt-header-icon.l {
    margin-top: 10px; }
  .bolt-header-icon.s {
    margin-top: 6px; }

.bolt-header-separator {
  height: 1px;
  background-color: rgba( 244, 244, 244 ,  1 );
  background-color: rgba( var(--palette-neutral-4,244, 244, 244) ,  1 ); }

.bolt-button.bolt-header-back-button {
  background-color: transparent;
  font-size: 1rem;
  margin-right: 8px; }
  @media screen and (max-width: 600px) {
    .bolt-button.bolt-header-back-button {
      padding: 0px; }
      .bolt-button.bolt-header-back-button .bolt-button-text {
        font-size: 0.75rem;
        color: rgba(0, 0, 0, .55);
        color: var(--text-secondary-color,rgba(0, 0, 0, .55));
        font-weight: normal; } }
